import { Component, OnInit } from '@angular/core';
import { StudentService } from '../../services/student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-semesterwise-result',
  templateUrl: './semesterwise-result.component.html',
  styleUrls: ['./semesterwise-result.component.css']
})
export class SemesterwiseResultComponent implements OnInit {

  particularSemSubjects = [];
  semNo;
  loader = false;
  universityRankLoader = false;
  totalMarks: number = 0;
  marksOutOf: number = 0;
  marksPercentage: number = 0;
  studentData: any;
  creditScored: number = 0;

  constructor(private studentService: StudentService, private router: Router) { }

  ngOnInit(): void {
    this.getSemesterResultData();
  }

  getSemesterResultData() {

    this.studentService.sendDataToSemWiseResult.subscribe((result) => {
      this.particularSemSubjects = result.subjects;
      this.semNo = result.semNo;
      this.studentData = result.studentData
      this.calculatePercntAndCredit();
      console.log("I am called", result);
    });
    console.log(this.semNo);
  }

  collegeRank() {
    this.loader = true;
    this.studentService.studentSem = this.semNo;
    this.studentService.getStudentCollegeRank();
  }
  
  universityRank() {
    this.universityRankLoader = true;
    this.studentService.studentSem = this.semNo;
    this.studentService.getUniversityRank();
  }

  checkBack(totalMarks){
    let total=parseInt(totalMarks);
    if(total<40)
    return true;
    return false;
  }

  calculatePercntAndCredit() {
    this.totalMarks = 0;
    this.marksOutOf = 0;
    this.marksPercentage = 0;
    this.creditScored = 0;

    for (let subject of this.particularSemSubjects) {
      this.totalMarks += parseInt(subject.subjectexternalm);
      this.marksOutOf += parseInt(subject.subjectwholem);
      this.creditScored += parseInt(subject.credit);
      this.marksPercentage = (this.totalMarks / this.marksOutOf) * 100;
    }
  }



}
