import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ConfigService } from './config.service';
import { Subject, ReplaySubject } from 'rxjs';


@Injectable()

export class StudentService {

    path: string = this.configService.path;

    studentResult;
    noOfBacklogs;
    Backlogs=[];
    overAllResult = [];
    sendDataToSemWiseResult: ReplaySubject<any> = new ReplaySubject(1);
    initialSemData;

    studentSem: string;
    collegeStudentsRankInfo;
    universityStudentRankInfo;

    constructor(private http: HttpClient, private router: Router, private configService: ConfigService) {

    }

    // To get student Result
    getStudentResult(enrollment: object) {
        return this.http.post(this.path + "/dosearchS", enrollment);
    }

    sendToSemWise(semesterWiseResult, semNo, studentData) {
        this.sendDataToSemWiseResult.next({ subjects: semesterWiseResult, semNo: semNo, studentData: studentData });
    }

    getStudentCollegeRank() {
        this.http.post(this.path + '/collegewiseranks', {
            "batch": this.studentResult.DOCS.batch,
            "collegecode": this.studentResult.DOCS.institutecode,
            "branch": this.studentResult.DOCS.programmecode,
            "semester": this.studentSem
        }).subscribe((students) => {
            this.collegeStudentsRankInfo = students;
            this.router.navigate(['/studentResult/collegeRank']);
        });
    }

    getUniversityRank() {
        this.http.post(this.path + '/Universityranks', {
            "batch": this.studentResult.DOCS.batch,
            "branch": this.studentResult.DOCS.programmecode,
            "semester": this.studentSem
        }).subscribe((students) => {
            this.universityStudentRankInfo = students;
            this.router.navigate(['/studentResult/universityRank']);
        });
    }
}
