import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { StudentService } from '../services/student.service';
import { UniversityService } from '../services/university.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-university-wise',
  templateUrl: './university-wise.component.html',
  styleUrls: ['./university-wise.component.css']
})
export class UniversityWiseComponent implements OnInit {

  textContent = true;
  loader = false;
  programmeDetails=[];
  universityWiseForm: FormGroup;

  constructor(private universityService: UniversityService, private router: Router) { }

  getProgrammeDeatils(){
    this.universityService.getProgrammmeDetails().subscribe((progDetails:any)=>{
      this.programmeDetails=progDetails;
      this.loader=false;
      console.log(this.programmeDetails);
    });
  }
  ngOnInit(): void {
    this.loader=true;
    this.collegeWiseFormGroup();
    this.getProgrammeDeatils();
  }


  collegeWiseFormGroup() {
    this.universityWiseForm = new FormGroup({
      'batch': new FormControl(null, [Validators.required]),
      'collegecode': new FormControl(null, [Validators.required]),
      'branch': new FormControl(null, [Validators.required]),
      'semester': new FormControl(null, [Validators.required]),
    })

    this.universityWiseForm.controls['batch'].setValue('2016', {onlySelf: true});
    this.universityWiseForm.controls['branch'].setValue('027', {onlySelf: true});
    this.universityWiseForm.controls['semester'].setValue('01', {onlySelf: true});
  }

  collegeWiseSearch() {
    this.loader = true;
    this.universityService.getUniversityRanks(this.universityWiseForm.value).subscribe((students) => {
      this.universityService.universityStudentRankInfo = students;
      this.universityService.studentSem = this.universityWiseForm.value.semester;
      this.loader = false;
      this.router.navigate(['universityWise/universityWiseRank']);
    });
  }

}
