import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { HttpClientModule } from "@angular/common/http";

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { HomeComponent } from './home/home.component';

import { StudentResultComponent } from './student-result/student-result.component';
import { SemesterwiseResultComponent } from './student-result/semesterwise-result/semesterwise-result.component';
import { OverAllResultComponent } from './student-result/over-all-result/over-all-result.component';
import { CollegeWiseComponent } from './college-wise/college-wise.component';
import { UniversityWiseComponent } from './university-wise/university-wise.component';
import { CollegeRankComponent } from './student-result/college-rank/college-rank.component';
import { UniversityRankComponent } from './student-result/university-rank/university-rank.component';

import { CollegeWiseRankComponent } from './college-wise/college-wise-rank/college-wise-rank.component';
import { UniversityWiseRankComponent } from './university-wise/university-wise-rank/university-wise-rank.component';

//CollegeGraph
import { CollegeWiseGraphComponent } from './college-wise-graph/college-wise-graph.component';
import { CollegeGraphComponent } from './college-wise-graph/college-graph/college-graph.component';

//     ************Services**************
import { CollegeService } from './services/college.service';
import { UniversityService } from './services/university.service';
import { StudentService } from './services/student.service';
import { ConfigService } from './services/config.service';
import { CollegeGraphDataService } from './services/collegegraphdata.service';
import { StudentBacklogsComponent } from './student-result/student-backlogs/student-backlogs.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    HomeComponent,
    StudentResultComponent,
    SemesterwiseResultComponent,
    OverAllResultComponent,
    CollegeRankComponent,
    UniversityRankComponent,
    CollegeWiseComponent,
    UniversityWiseComponent,
    CollegeWiseRankComponent,
    UniversityWiseRankComponent,
    CollegeWiseGraphComponent,
    CollegeGraphComponent,
    StudentBacklogsComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    CommonModule,
    FormsModule,
    HttpClientModule,
    ReactiveFormsModule
  ],
  providers: [
    StudentService,
    ConfigService,
    CollegeService,
    UniversityService,
    CollegeGraphDataService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
