import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ConfigService } from './config.service';
import { Observable } from 'rxjs';

export interface CollegeAvg {
  SubjectCode: string;
  SubjectName: string;
  InternalMarks: number;
  ExternalMarks:number;
}

@Injectable({
  providedIn: 'root'
})


export class CollegeGraphDataService {

  path: string = this.configService.path;
  data=[];
  getData:any;


constructor(private configService: ConfigService,private http: HttpClient, private router: Router) { }

private generateRandomValue(start: number, end: number) {
  return Math.ceil(Math.random() * (end - start) + start);
}

getCollegeAvgMarks(collegedata){
  return this.http.post(this.path + '/CollegeAverageMarks', collegedata)
}

}
