import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { StudentService } from '../services/student.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  searchForm: FormGroup;
  loader = false;
  textContent = true;

  constructor(private studentService: StudentService, private router: Router) { }

  ngOnInit(): void {
    this.textContent = true;
    console.log("home")
    this.searchForm = new FormGroup({
      'enrollment': new FormControl(null, [Validators.required])
    })
  }
  
  OnSearch() {
    this.router.navigate(['/']);
    this.loader = true;
    this.studentService.getStudentResult(this.searchForm.value).subscribe((result) => {
      this.textContent = false;
      this.studentService.studentResult = result;
      this.studentService.noOfBacklogs=result['BACKLOGS'];
      this.studentService.Backlogs=result['SEMBACK'];
      this.loader = false;
      this.router.navigate(['studentResult']);
    }, (err) => {
      this.loader = false;
      console.log(err);
    });
  }



}
