import { Injectable } from '@angular/core';
import { ConfigService } from './config.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Injectable()

export class UniversityService {
    path: string = this.configService.path;

    universityStudentRankInfo;
    studentSem: string;

    constructor(private http: HttpClient, private router: Router, private configService: ConfigService) { }

    getUniversityRanks(universityData) {
        return this.http.post(this.path + '/Universityranks', universityData)
    }

    getProgrammmeDetails() {
        return this.http.get(this.path + '/getprogrammedetails');
    }
}