import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { StudentService } from '../services/student.service';
import { CollegeService } from '../services/college.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-college-wise',
  templateUrl: './college-wise.component.html',
  styleUrls: ['./college-wise.component.css']
})
export class CollegeWiseComponent implements OnInit {

  loader=false;
  textContent = true;
  collegeWiseForm: FormGroup;
  collgeDetails=[];
  programmeDetails=[];

  constructor(private collegeService:CollegeService, private router:Router) { }

  ngOnInit(): void {
    this.loader=true;
    this.collegeWiseFormGroup();
    this.getCollegeDetails();
    this.getProgrammeDeatils();
  }

  getCollegeDetails(){
    this.collegeService.getCollegeDetails().subscribe((clgDetails:any)=>{
      this.collgeDetails=clgDetails;
        this.loader=false;
      console.log(this.collgeDetails);
    })
  }
  getProgrammeDeatils(){
    this.collegeService.getProgrammmeDetails().subscribe((progDetails:any)=>{
      this.programmeDetails=progDetails;
      this.loader=false;
      console.log(this.programmeDetails);
    });
  }
  collegeWiseFormGroup() {
    this.collegeWiseForm = new FormGroup({
      'batch': new FormControl(null, [Validators.required]),
      'collegecode': new FormControl(null, [Validators.required]),
      'branch': new FormControl(null, [Validators.required]),
      'semester': new FormControl(null, [Validators.required]),
    })

    this.collegeWiseForm.controls['batch'].setValue('2016', {onlySelf: true});
    this.collegeWiseForm.controls['collegecode'].setValue('208', {onlySelf: true});
    this.collegeWiseForm.controls['branch'].setValue('027', {onlySelf: true});
    this.collegeWiseForm.controls['semester'].setValue('01', {onlySelf: true});
  }

  collegeWiseSearch() {
    console.log(this.collegeWiseForm.value);
    this.loader=true;
    this.collegeService.getCollegeRanks(this.collegeWiseForm.value).subscribe((students) => {
      this.collegeService.collegeStudentsRankInfo = students;
      this.collegeService.studentSem = this.collegeWiseForm.value.semester;
      this.loader=false;
      this.router.navigate(['collegeWise/collegeWiseRank']);
  });
  }


}
