import { Component, OnInit } from '@angular/core';
import { CollegeGraphDataService } from '../services/collegegraphdata.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { CollegeService } from '../services/college.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-college-wise-graph',
  templateUrl: './college-wise-graph.component.html',
  styleUrls: ['./college-wise-graph.component.css']
})
export class CollegeWiseGraphComponent implements OnInit {

  loader=false;
  textContent = true;
  collegeGraphForm: FormGroup;
  collgeDetails=[];
  programmeDetails=[];

  constructor(private collegeService:CollegeService, private router:Router, private GraphSerivce: CollegeGraphDataService) {
  }

  ngOnInit(): void {
    this.loader=true;
    this.collegeGraphFormGroup();
    this.getCollegeDetails();
    this.getProgrammeDeatils();
  }


  getCollegeDetails(){
    this.collegeService.getCollegeDetails().subscribe((clgDetails:any)=>{
      this.collgeDetails=clgDetails;
        this.loader=false;
      console.log(this.collgeDetails);
    })
  }

  getProgrammeDeatils(){
    this.collegeService.getProgrammmeDetails().subscribe((progDetails:any)=>{
      this.programmeDetails=progDetails;
      this.loader=false;
      console.log(this.programmeDetails);
    });
  }

  collegeGraphFormGroup(){
    this.collegeGraphForm = new FormGroup({
      'batch': new FormControl(null, [Validators.required]),
      'collegecode': new FormControl(null, [Validators.required]),
      'branch': new FormControl(null, [Validators.required]),
      'semester': new FormControl(null, [Validators.required]),
    })
    this.collegeGraphForm.controls['batch'].setValue('2016', {onlySelf: true});
    this.collegeGraphForm.controls['collegecode'].setValue('208', {onlySelf: true});
    this.collegeGraphForm.controls['branch'].setValue('027', {onlySelf: true});
    this.collegeGraphForm.controls['semester'].setValue('01', {onlySelf: true});
  }

  CollegeGraphAvgSearch(){
      console.log(this.collegeGraphForm.value);
      this.loader=true;
      this.GraphSerivce.getCollegeAvgMarks(this.collegeGraphForm.value).subscribe((CollegeAverageMarks) => {
        this.GraphSerivce.getData=CollegeAverageMarks;
        for (let i = 1; i < this.GraphSerivce.getData.length; i++) {
          this.GraphSerivce.data.push({
            SubjectCode: CollegeAverageMarks[i].SubjectCode,
            SubjectName: CollegeAverageMarks[i].SubjectName,
            InternalMarks: CollegeAverageMarks[i].InternalMarks,
            ExternalMarks:CollegeAverageMarks[i].ExternalMarks,
          });
        }
        this.loader=false;
        this.router.navigate(['/collegeWiseGraph/collegeGraph']);
        });  
      }
    
    
}
